import { AuthProvider } from './../../providers/auth/auth';
import { Component } from '@angular/core';
import { NavController, NavParams, ViewController, IonicPage } from 'ionic-angular';
import { GlobalProvider } from '../../providers/global/global';
import { RestProvider } from '../../providers/rest/rest';

/**
 * Generated class for the CartPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-cart',
  templateUrl: 'cart.html',
})
export class CartPage {
  items: any;
  filtereditems:any;
  productNumber: number;
  price: number;
  searchTerm: string = '';
  hideButton: boolean = false;
  errorCounter: number;
  productLeave: any;
  productReorder: any;
  buttonLeave: boolean;
  buttonReorder: boolean;

  images: Array<string> = ["CP8100","CP8101","CP8102","CP8103","CP8104","CP8105","CP8106","CP8200","CP8201","CP8202","CP8203","CP8204","CP8205","CP8206","CP8300","CP8301","CP8302","CP8303","CP8304","CP8305","CP8306","CP8400","CP8401","CP8402","CP8403","CP8404","CP8405","CP8406","CP8500","CP8501","CP8502","CP8503","CP8504","CP8505","CP8506","CP8600","CP8601","CP8602","CP8603","CP8604","CP8605","CP8606","CP8700","CP8701","CP8702","CP8703","CP8704","CP8705","CP8706","CP8800","CP8801","CP8802","CP8803","CP8804","CP8805","CP8806","CP9100","CP9101","CP9102","CP9103","CP9104","CP9105","CP9106","CP9200","CP9201","CP9202","CP9203","CP9204","CP9205","CP9206","CP9300","CP9301","CP9302","CP9303","CP9304","CP9305","CP9306","CP9400","CP9401","CP9402","CP9403","CP9404","CP9405","CP9406","CP9500","CP9501","CP9502","CP9503","CP9504","CP9505","CP9506","CP9600","CP9601","CP9602","CP9603","CP9604","CP9605","CP9606","CP9700","CP9701","CP9702","CP9703","CP9704","CP9705","CP9706","CP9800","CP9801","CP9802","CP9803","CP9804","CP9805","CP9806"];

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams, 
    public view: ViewController,
    public global: GlobalProvider,
    public rest: RestProvider,
    public auth: AuthProvider
  ) {
    this.items = [];
    this.productLeave = []; 
    this.productReorder = [];
    this.buttonLeave = false;
    this.buttonReorder = false;

  }

  ionViewWillEnter(){
    if (!this.auth.authenticated()) {
      this.auth.logout();
      this.navCtrl.setRoot('LoginPage');
      return;
    }
  }

  filterItems(){
    console.log(this.searchTerm);
    if (this.searchTerm.length < 2){
      this.items = [];
      return;
    }

    this.rest.searchProduct(this.searchTerm).then(
      (scs:any) => {
        this.items = [];
        scs.products.forEach(element => {
          this.items.push({
            id: element.id,
            title: element.name,
            price: element.price,
            image: element.image,
            quantity: 1,
            color: 'CP8100',
          });
        });
        
      },
      (err) => {
        console.log(err);
        
      }
    )


  }

  productOrder(id, title, price, image) {
    this.global.productCart.push({
      id: id,
      title: title, 
      price: price,
      image: image,
      quantity: 1,
      color: 'CP8100',
    });

    this.productLeave[id] = false;
    this.productReorder[id] = false;

    this.searchTerm = '';
    this.items = [];
  }
  
  CheckOut() {
    this.errorCounter = 0;
    if(this.global.productCart == null){
      alert('Bạn chưa thêm sản phẩm nào');
    }
    else {
      for (let i of this.global.productCart) {
        if (i.quantity < 1) {
          this.errorCounter++;
        }
      }
      if(this.errorCounter > 0) {
        alert('Không được để số lượng sản phẩm bằng 0');
      }
      else {
        this.navCtrl.push('CheckOutPage');
      }
    }
  }
  
  closeCard(product) {
    this.buttonLeave = false;
    this.buttonReorder = false;
    let index = this.global.productCart.indexOf(product);
    this.productLeave[product.id] = true;
    if (index > -1 && this.global.productCart.length === 1) {
      this.buttonLeave = true;
    }
    setTimeout(()=> {
      if(index > -1){
        this.global.productCart.splice(index, 1);
        for (let i = index; i < this.global.productCart.length; i++) {
          this.productReorder[this.global.productCart[i].id] = true;
          if (this.global.productCart.length > 0) {
            this.buttonReorder = true;
          }
        }
      }
    }, 1000); 
    setTimeout(() => {
      this.buttonLeave = false;
      this.buttonReorder = false;
    }, 2400);
  }

	prepareImageWithLabelSelector() {
		setTimeout(() => {
			let buttonElements = document.querySelectorAll('div.alert-radio-group button');
			if (!buttonElements.length) {
				this.prepareImageWithLabelSelector();
			} else {
				for (let index = 0; index < buttonElements.length; index++) {
					let buttonElement = buttonElements[index];
					let optionLabelElement = buttonElement.querySelector('.alert-radio-label');
					let image = optionLabelElement.innerHTML.trim();
					buttonElement.classList.add('imagewithlabelselect', 'image_' + image);
				}
			}
		}, 100);
	}

	setImageWithLabel(image) {
		console.log('Selected image with label is', image);
	}

}



